@import url('./fonts.css');

/* css variables */

:root {
    --white: #fff;
    --black: #0f0f14;
    --light-text: #ffffffcc;
    --dark-text: #000;
    --light-border: #e5e5e6;
    --dark-border: #333;
    --light-gray: #f5f5f7;
    --dark-gray: #f5f5f70a;
    /* --light-blue: #009fed;
    --dark-blue: #4c6dee; */
    --text: #000000cc;
    --gradient: linear-gradient(to right, #a975ff 3.48%, #7875ff 61.23%, #6b9eff 118.98%);
}

/* main styles */

* {
    box-sizing: border-box;
    font-family: 'graphik';
    margin: 0;
    padding: 0;
    user-select: none;
    -webkit-tap-highlight-color: transparent;
}

body {
    overflow-x: hidden;
    min-width: 320px;
    position: relative;
}

@media (max-width: 320px) {
    body {
        overflow-x: auto;
    }
}

button, input {
    border: none;
    outline: none;
    background: none;
}

button {
    cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus {
  -webkit-text-fill-color: var(--black);
  transition: background-color 5000s ease-in-out 0s;
}

/* styles */

.wrapper {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    background-color: var(--white);
}

.wrapper-dark {
    background-color: var(--black);
}

.container {
    width: 1440px;
    min-width: 320px;
    padding: 0 70px;
}

/* header */

.header {
    width: 100%;
    height: 55px;
    display: flex;
    justify-content: space-between;
    padding: 6px 0;
}

.logo {
    display: flex;
    align-items: center;
}

.logo-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    color: #332d54;
}

.logo-title-dark {
    color: #f6f4fd;
}

.menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}   

.nav {
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    color: var(--text);
    text-decoration: none;
    cursor: pointer;
    transition: .15s linear;
}

.nav:hover {
    color: #e049f9;
}

.nav-dark {
    color: var(--light-text);
}

.nav-dark:hover {
    color: #caa6ff;
}

.btn-mode {
    margin: 0 19px;
}

.btn-mode svg path {
    transition: .15s linear;
}

.btn-mode:hover  svg path {
    fill: #e049f9;
}

.btn-mode-dark:hover svg path {
    fill: #caa6ff;
}

.btn-menu {
    height: 43px;
    border-radius: 8px;
    background-image: linear-gradient(var(--white), var(--white)),  var(--gradient);
    text-decoration: none;
    border: 1px solid transparent;
    background-origin: border-box;
    background-clip: content-box, border-box;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .15s linear;
}

.btn-menu span {
    width: 100%;
    border-radius: 8px;
    padding: 0 16px 0 19px;
    background-image: var(--gradient);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    transition: .15s linear;
}

.btn-menu-dark {
    background-image: linear-gradient(var(--black), var(--black)),  var(--gradient);
}

.btn-menu:hover {
    opacity: .8;
}

/* website short info styles */

.title-wrapper {
    width: 100%;
    margin: 28px 0 36px;
    background: var(--light-gray);
    border-radius: 20px;
}

.title-wrapper-dark {
    background: var(--dark-gray);
}

.title-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 36px 250px;
    border-radius: 20px;
    background-image: url(../icons/title-bg.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.title {
    font-weight: bold;
    font-size: 36px;
    line-height: 40px;
    color: #24243c;
    text-align: center;
}

.title-dark {
    color: #f2f2fb;
}

.description {
    font-weight: normal;
    font-size: 16px;
    line-height: 18px;
    color: rgba(0, 0, 0, 0.72);
    text-align: center;
    padding-top: 15px;
}

.description-dark {
    color: var(--light-text);
}

/* items styles */

.tabs {
    width: 100%;
    display: flex;
    align-items: center;
    padding-bottom: 16px;
    overflow-y: auto;
    scrollbar-width: none;
}

.tabs::-webkit-scrollbar {
    display: none;
}

.btn-tab {
    width: fit-content;
    height: 35px;
    padding: 0 16px;
    font-weight: normal;
    font-size: 14px;
    line-height: 100%;
    color: #535371;
    margin-right: 13px;
    background-color: #f0f1fb;
    border-radius: 8px;
    transition: .15s linear;
}

.btn-tab:hover {
    color: rgba(83, 83, 113, 0.8);
}

.btn-tab:last-child {
    margin-right: 0;
}

.btn-tab-dark {
    background-color: rgba(240, 241, 251, 0.08);
    color: #e7e7f5;
}

.btn-tab-dark:hover {
    color: rgba(231, 231, 245, 0.8);
}

.btn-tab-active {
    background-color: #7875ff;
    color: #fff;
}

.btn-tab-active:hover {
    background-color: rgba(105, 102, 255, 0.8);
    color: #fff;
}

.btn-tab-active-dark:hover {
    background-color: rgba(87, 84, 255, 0.8);
    color: #fff;
}

.items {
    width: 100%;
    border: 1px solid var(--light-border);
    border-radius: 11px;
    overflow-y: auto;
    scrollbar-width: none;
    margin-bottom: 32px;
}

.items::-webkit-scrollbar {
    display: none;
}

.items-dark {
    border: 1px solid var(--dark-border);
}

.items-titles {
    width: 100%;
    min-width: 860px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.item-text {
    width: calc(100% / 6);
    font-weight: normal;
    font-size: 14px;
    line-height: 15px;
    color: #000;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 17px;
}
.item-text:first-child {
    justify-content: flex-start;
}

.item-text svg {
    margin-left: 5px;
}

.item-text-dark {
    color: #fff;
}

.item-text svg path {
    stroke: #000;
}

.item-text-dark svg path {
    stroke: #fff;
}

.item {
    width: 100%;
    min-width: 860px;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(229, 229, 230, 0.7);
}

.item:last-child {
    border-bottom: none;
}

.item-dark {
    border-bottom: 1px solid rgba(51, 51, 51, 0.7);;
}

.item-name {
    font-weight: bold;
}

.item-index {
    font-weight: normal;
    opacity: .4;
}

.item-logo {
    width: 22px;
    height: 22px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin: 0 16px;
}

.item-chain {
    width: 24px;
    height: 24px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin-left: 2px;
}


.item-btn {
    height: 26px;
    border-radius: 5px;
    background-image: linear-gradient(var(--white), var(--white)), var(--gradient);
    text-decoration: none;
    border: 1px solid transparent;
    background-origin: border-box;
    background-clip: content-box, border-box;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.item-btn-dark {
    background-image: linear-gradient(var(--black), var(--black)), var(--gradient);
}

.item-btn span {
    width: 100%;
    border-radius: 5px;
    padding: 0 8.5px 0 12px;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: var(--black);
    transition: .15s linear;
}

.item-btn svg {
    margin-left: 4.5px;
}

.item-btn-dark span {
    color: var(--white);
}

.item-btn-dark svg path {
    stroke: var(--white);
    transition: .15s linear;
}

.item-btn:hover span {
    background-image: var(--gradient);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
}

.item-btn:hover svg path {
    stroke: #6b9eff;
}

.item-loader-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px 0;
}

.item-loader {
    width: 48px;
    height: 48px;
    border: 6px solid #f6f6f8;
    border-radius: 50%;
    border-top: 6px solid #009fed;
    -webkit-animation: spin 1.5s linear infinite;
    animation: spin 1.5s linear infinite;
}

.item-loader-dark {
    border: 6px solid rgba(245, 245, 247, 0.04);
    border-top: 6px solid #009fed;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* styles for touch screen device */

@media (pointer: coarse) {
    .nav:hover {
        color: var(--text);
    }
    .nav-dark:hover {
        color: var(--light-text);
    }
    .btn-mode:hover svg path {
        fill: var(--black);
    }
    .btn-mode-dark:hover svg path {
        fill: var(--white);
    }
    .btn-menu:hover {
        opacity: 1;
    }
    .btn-tab:hover {
        color: #535371;
    }
    .btn-tab-dark:hover {
        color: #e7e7f5;
    }
    .btn-tab-active:hover {
        background-color: #7875ff;
        color: #fff;
    }
    .btn-tab-active-dark:hover {
        background-color: #7875ff;
        color: #fff;
    }
    .item-btn:hover span, .item-btn-dark:hover span {
        background-image: none;
        -webkit-background-clip: none;
        -moz-background-clip: none;
        -webkit-text-fill-color: inherit;
        -moz-text-fill-color: inherit;
    }    
    .item-btn:hover svg path {
        stroke: var(--black);
    } 
    .item-btn-dark:hover svg path {
        stroke: var(--white);
    }
}

/* Media queries */

@media (max-width: 1440px) {
    .container {
        width: 1160px;
        padding: 0 20px;
    }
    .title-box {
        padding: 36px 96px;
    }
}

@media (max-width: 1180px) {
    .container {
        width: 1024px;
    }
    .tabs {
        padding-bottom: 14px;
    }
    .btn-tab {
        height: 32px;
        padding: 0 14px;
        font-size: 13px;
        line-height: 14px;
        margin-right: 10px;
    }
    .items {
        border-radius: 10px;
    }
    .items-titles {
        height: 42px;
    }
    .item-text {
        font-size: 13px;
        line-height: 14px;
        padding: 0 14px;
    }
    .item-text svg {
        margin-left: 4px;
    }
    .item {
        height: 52px;
    }
    .item-logo {
        width: 20px;
        height: 20px;
        margin: 0 14px;
    }
    .item-chain {
        width: 22px;
        height: 22px;
    }
    .item-btn {
        height: 24px;
    }
    .item-btn span {
        padding: 0 8px 0 11px;
        font-size: 13px;
        line-height: 15px;
    }
    .item-btn svg {
        margin-left: 4px;
    }
    .item-loader-box {
        padding: 14px 0;
    }
    .item-loader {
        width: 45px;
        height: 45px;
        border: 5px solid #f6f6f8;
        border-top: 5px solid #009fed;
    }
    .item-loader-dark {
        border: 5px solid rgba(245, 245, 247, 0.04);
        border-top: 5px solid #009fed;
    }
}

@media (max-width: 1024px) {
    .container {
        width: 100%;
    }
    .nav {
        font-size: 14px;
        line-height: 16px;
    }
    .btn-mode {
        margin: 0 14px;
    }
    .btn-menu {
        height: 36px;
        border-radius: 6px;
    }
    .btn-menu span {
        border-radius: 6px;
        padding: 0 12px;
        font-size: 14px;
        line-height: 16px;
    }
    .title-wrapper {
        margin: 24px 0 30px;
        border-radius: 18px;
    }
    .title-box {
        padding: 34px;
    }
    .title {
        font-size: 28px;
        line-height: 32px;
    }
    .description {
        font-size: 14px;
        line-height: 16px;
    }
}

@media (max-width: 680px) {
    .title-box {
        padding: 25px;
        background-image: none;
    }
    .title {
        font-size: 24px;
        line-height: 28px;
    }
}

@media (max-width: 440px) {
    .container {
        padding: 0 12px;
    }
    .header {
        height: 48px;
    }
    .logo-title {
        font-size: 14px;
        line-height: 16px;
    }
    .nav {
        font-size: 13px;
        line-height: 15px;
    }
    .btn-mode {
        margin: 0 10px;
    }
    .btn-menu {
        height: 34px;
        max-width: 80px;
    }
    .btn-menu span {
        padding: 0 10px;
        font-size: 13px;
        line-height: 15px;
    }
}
